<script>
  import Field from "../ui/Field.svelte";
  import Table from "../ui/Table.svelte";
  export let input = "";
  export let output = "";


  function cjkCount(word) {
   let len = 0;
    for (var i = 0; i < word.length; i++) {
      if (word.charAt(i).match(/[\u4e00-\u9fa5]/)|| word.charAt(i).match(/[\u9FA6-\u9fcb]/)) {
        len += 1
      }
    }
    return len
  }

  function getData(word) {
    return [
      {
        type: "",
        count: word.length,
      },
      { type: "CJK", count: cjkCount(word) },
      {
        type: "",
        count: (word.match(/\n/g) && word.match(/\n/g).length + 1) || 0,
      },
      {
        type: "",
        count: word.replace(/\s/g, "").length || 0,
      },
    ];
  }

  let col = [
    { label: "", prop: "type" },
    { label: "", prop: "count" },
  ];
</script>


  <div class="columns">
    <div class="column">
      <Field label="">
        <Table data={getData(input)} columns={col} />
      </Field>
    </div>
    <div class="column">
      <Field label="">
        <Table data={getData(output)} columns={col} />
      </Field>
    </div>
  </div>

