<script>
  import "bulma/css/bulma.css";
  import "bulma-checkradio/dist/css/bulma-checkradio.min.css";
  //
  import { Field, Tabs, NavBar, Footer } from "./ui/index.svelte";
  import * as Icon from "./ui/icon";
  import * as pkg from "../package.json";
  //
  import {
    Reg,
    Encode,
    Editor,
    State,
    Format,
  } from "./components/index.svelte";

  let input = "";
  let output = "";
  let currentTab = "1";

  let tabs = [
    { value: "1", text: "" },
    { value: "2", text: "" },
    { value: "3", text: "" },
    { value: "4", text: "" },
    { value: "5", text: "" },
  ];
  let compoents = {
    "1": Reg,
    "2": Encode,
    "3": Editor,
    "4": Format,
    "5": State,
  };

  function copy() {
    let obj = document.getElementById("output");
    obj.focus();
    obj.select();
    try {
      if (document.execCommand("copy", false, null)) {
      } else {
        console.log(" execCommand");
      }
    } catch (e) {
      console.log(e);
    }
  }

  function copyToInput() {
    input = output;
  }

  // PWA  Worker
  if ("serviceWorker" in navigator) {
    navigator.serviceWorker.register("./serviceworker.js",{scope:"./"}).catch((e) => {
      console.log("ServiceWorker registration failed", e);
    });
  }
</script>

<main>
  <NavBar hasShadow="true">
    <div slot="navbar-brand" class="navbar-item">
      <img src="./images/apple-touch-icon.png" alt="logo" />&nbsp;
      <h2></h2><span class="tag is-primary is-light" style="margin-left:8px" >ver.{pkg.version}</span>
    </div>
    <div slot="navbar-end" class="navbar-item">
      <div class="field is-grouped">
        <div class="control">
          <a
            class="button is-link is-light"
            href="https://github.com/owlzou/text-tool"
            target="_blank"
          >
            {@html Icon.github}
            <span>GitHub</span></a
          >
        </div>
      </div>
    </div>
  </NavBar>
  <!--  -->
  <section>
    <div class="container">
      <Field label="">
        <textarea class="textarea" placeholder="" bind:value={input} />
      </Field>
      <Tabs bind:value={currentTab} options={tabs} />
      <svelte:component this={compoents[currentTab]} {input} bind:output />
      <hr />
      <Field label="">
        <textarea id="output" class="textarea" placeholder=""
          >{output}</textarea
        >
      </Field>
      <button class="button is-primary" on:click={copy}>
        {@html Icon.copy}</button
      >
      <button class="button is-light" on:click={copyToInput}
        >{@html Icon.arrowNarrowUp}</button
      >
    </div>
  </section>
  <Footer>
    <div> 2020-{new Date().getFullYear()}</div>
  </Footer>
</main>

<style>
  .container {
    padding: 20px;
  }
  :global(.button .icon-tabler) {
    margin-right: 5px !important;
  }
  #output {
    font-family: "sarasa mono sc", "lxgw wenkai mono", "Courier New", Courier,
      monospace;
  }
</style>
